import React, { useEffect } from 'react'
import '../css/card.css'
import github from '../img/github.svg'
export default function Card({img,link_github,titulo,descripcion,lenguajes,link_web}){
    const link = React.createRef()
    const see_github = React.createRef()
    useEffect(()=>{
        const linkaux = link.current
        linkaux.addEventListener('mouseover',()=>{
            see_github.current.style.display="block"
        })
        linkaux.addEventListener('mouseout',()=>{
            see_github.current.style.display="none"
        })
        return ()=>{
            linkaux.removeEventListener('mouseover')
            linkaux.removeEventListener('mouseout')
        }
    })
    return (
        <article className="card">
            <figure>
                <img className="card_img" src={img} alt="imagen proyecto" />
            </figure>
            <p ref={see_github} className="card_see_on_github">
                See on Github
            </p>
            <a href={link_github} target="_blank" rel="noreferrer" className="card_a">
                <img ref={link} className="card_github" src={github} alt="github link" />
            </a>
            <h3 className="card_h3">{titulo}</h3>
            <p className="card_p">{descripcion}</p>
            {
                link_web && <div className="container_link_web"><a className="link_web" href={link_web} target="_blank" rel="noreferrer">
                    CHECK OUT</a></div>
            }
            {
                link_web ? <div className="container_lenguajes">
                {
                    lenguajes.map(item=>{
                        return <span className="lenguajes">{item}</span>
                    })
                }
                </div> : <div className="container_lenguajes-fondo">
            {
                lenguajes.map(item=>{
                    return <span className="lenguajes">{item}</span>
                })
            }
            </div>
            }
        </article>
    )
}